.App {
  text-align: center;
  padding: 20px;
}

html,
body {
  margin: 0;
  padding: 0;
  height: 100%;
  background: -webkit-linear-gradient(
    to bottom,
    rgba(0, 0, 0, 0.5),
    rgba(255, 255, 255, 0.5)
  ); /* For Chrome and Safari */
  background: linear-gradient(
    to bottom,
    rgba(0, 0, 0, 0.5),
    rgba(255, 255, 255, 0.5)
  ); /* Standard */
  background-attachment: fixed;
  background-repeat: no-repeat;
  background-size: cover;
}

.layers-container {
  flex: 1;
  display: flex;
  flex-direction: column;
  justify-content: space-between;
  position: relative;
  margin-top: 125px;
  margin: 100px auto;
  height: 450px; /* Adjust the height of the circular arrangement */
  width: 450px; /* Adjust the width of the circular arrangement */
  overflow: hidden;
  border-radius: 10%; /* Depends on the size of the layer */
  background-image: linear-gradient(
      rgba(114, 30, 13, 0.2),
      rgba(214, 30, 23, 0.7)
    ),
    url(upd.jpg);
  background-repeat: repeat-y;
  background-position: center;
  display: flex;
  flex-direction: column;
  align-items: center;
  animation: layers-container 15s linear infinite;
}

.layers-container img {
  width: 50%;
  height: auto;
}

@keyframes layers-container {
  100% {
    transform: rotate(360deg);
  }
}

.layer {
  position: absolute;
  width: 100px; /* Adjust the width and height to control the square size */
  height: 100px; /* Adjust the width and height to control the square size */
  border-radius: 45%; /* Make layers circular */
  animation: spinlayer 1.7s linear infinite;
  transition: background-color 0.3s;
  z-index: calc(
    300 - var(--depth)
  ); /* Set z-index dynamically based on depth */
}

@keyframes spinlayer {
  100% {
    transform: rotate(360deg);
  }
}

.layer:hover {
  background: radial-gradient(circle at center, #ff0000 20%, transparent 60%);
  transform: scale(5.9);
  transition: transform 5s ease;
  animation: spinlayer 4.7s ease infinite alternate;
  z-index: 2;
}

.layer.custom-hover {
  background-color: green; /* This will take precedence over the :hover effect */
  cursor: pointer; /* You can adjust the cursor style here */
}

.layer.hovered {
  background-color: green; /* You can adjust the styles here */
  cursor: pointer; /* You can adjust the cursor style here */
}
.layer-hovered {
  background: radial-gradient(circle at 50% 50%, #ff0000 50%, transparent 50%);
}

@keyframes spinlayer {
  100% {
    transform: rotate(360deg);
  }
}
